.news {
  &__container {
    padding: 40px 0;
    display: flex;
    flex-wrap: wrap;
  }
  &__more {
    background-color: #fff;
    text-decoration: none;
    display: block;
    width: 33.33%;
    background-color: #fff;
    text-align: center;
    background-color: rgba(255, 136, 0, 0.6);
  }
}
.news-block {
  width: 33.33%;
  &__container {
    display: block;
    position: relative;
    text-decoration: none;
    overflow: hidden;
    &:before {
      content: '';
      display: block;
      padding-top: 70%;
    }
    &:hover {
      .news-block__image {
        filter: grayscale(0);
      }
      .news-block__title {
        transform: translateY(-5px);
        background-color: rgba(255, 136, 0, 1);
      }
    }
  }
  &__image {
    position: absolute;
    display: block;
    width: 100%;
    height: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 0.3s;
    filter: grayscale(100%);
  }
  &__title {
    position: absolute;
    left: 15px;
    bottom: 15px;
    background-color: rgba(255, 136, 0, 0.6);
    color: #fff;
    font-weight: 700;
    font-size: 18px;
    max-width: calc(100% - 30px);
    padding: 10px;
    transition: 0.3s;
  }
}
@media all and(max-width: 1170px) {
  .news .container {
    padding: 0;
  }
  .news__container {
    padding-top: 0;
  }
}
@media all and(max-width: 768px) {
  .news-block {
    width: 50%;
  }
}
@media all and(max-width: 576px) {
  .news-block {
    width: 100%;
  }
}